body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(/E-Commerce_App/static/media/pexels-fwstudio-129731.0d084339.jpg);
  background-repeat:repeat-y;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


button{
  margin: 10px;
}

#items {
  height:-webkit-fit-content;
  height:-moz-fit-content;
  height:fit-content;
  width:70vw;
  border: 2px skyblue solid;
  border-radius: 4px;
  margin: 15px;
  display: flex;
  background-color:#f4f6f6;
}

#itemimg img{
  height: 150px;
  width: 160px;
  margin: 10px;
}

#specs{
  margin-left: 20px;
  display: flexbox;
  align-content:flex-end;
}

.nav{
  display: flexbox;
  justify-content: space-evenly;
  justify-items: center;
  height: 8vh;
}

h2{
  color: #410ee4;
}
h4{
  color: black;
}


#details{
  height:80vh;
  width: 80vw;
  display:flex;
  margin-top: 100px;
}

#detailsimg img{
  height:75vh;
  width: 60vh;
}

#detailsspecs{
  margin: 40px;
}
#detailsspecs button{
  width:80%;
}

#cartimg img{
  height: 150px;
  width: 150px;
}

#cartitem {
  display:flex;
}

#btn{
  display: flex;
  align-items: stretch;
}


